
.header{
    width:100%;
    height:235px;
    background-color: #fff;
    display:flex;
    flex-direction:column;
    justify-content: center;
    align-items: center;
    padding:25px 0px;
}

.header img{
    width:400px;
}





@media (max-width: 768px){
    .header{
        height:30vh;
        align-items: center;
        justify-content: center;
        width:100%;
    }
    .header img{
        width:295px;
        padding:0px 15px;
    }

}